import SalesChart from 'components/Chart/SalesChart';
import ErrorBoundary from 'components/ErrorBoundary/ErrorBoundary';
import {
  CartWraper,
  ChartWraper,
  ContentBox,
  MenagerWraper,
  SectionWraper,
  YearChartWraper,
} from './Dashboard.styled';
import YearChart from 'components/Chart/YearChart';
import Header from 'components/Header/Header';
import { useCallback, useEffect, useState } from 'react';
import axios from 'axios';
import AnimatedCounter from 'components/AnimatedCounter/AnimatedCounter';

export default function Dashboard() {
  const [orders, setOrders] = useState([]);
  const [clients, setClients] = useState([]);
  const [selectedYear] = useState(new Date());
console.log(process.env.REACT_APP_HOST_BACK);
  const actualSalesForYear = orders.reduce(
    (data, order) => {
      const orderYear = new Date(order.createdAt).getFullYear();
      if (orderYear === selectedYear.getFullYear()) {
        data.totalSales += Number(order.total_amount);
        data.totalOrders += 1;
      }
      return data;
    },
    { totalSales: 0, totalOrders: 0 }
  );
  const salesForYear = actualSalesForYear.totalSales;
  const numberOfOrdersForYear = actualSalesForYear.totalOrders;

  const fetchData = useCallback(async () => {
    try {
      const ordersResponse = await axios.get(
        `https://back-crm-fb781da88f45.herokuapp.com/onlyorders`
      );
      setOrders(ordersResponse.data);
      const clientsResponse = await axios.get(
        `https://back-crm-fb781da88f45.herokuapp.com/clients`
      );
      setClients(clientsResponse.data);
    } catch (error) {
      console.error('   ', error);
    }
  }, []);

  useEffect(() => {
    fetchData();
  }, [fetchData]);

  return (
    <>
      <Header
        textHead=""
        textBtn=" "
        handler={() => console.log('btn')}
      />
      <ContentBox>
        <p>  </p>
        <CartWraper>
          <div className="cart-item order">
            <AnimatedCounter
              value={numberOfOrdersForYear}
              text=" :"
            />
          </div>
          <div className="cart-item client">
            <AnimatedCounter value={clients.length} text=":" />
          </div>
          <div className="cart-item ash">
            <AnimatedCounter
              value={salesForYear.toFixed(2)}
              text="  :"
            />
          </div>

          <div className="cart-item chat">
            <AnimatedCounter value="3" text=" :" />
          </div>
        </CartWraper>
        <SectionWraper>
          <ChartWraper>
            <h2>  </h2>
            <ErrorBoundary>
              <SalesChart orders={orders} />
            </ErrorBoundary>
          </ChartWraper>
          <YearChartWraper>
            <h2>  </h2>
            <ErrorBoundary>
              <YearChart />
            </ErrorBoundary>
          </YearChartWraper>
          <MenagerWraper>
            <h2> </h2>
          </MenagerWraper>
        </SectionWraper>
        <SectionWraper>
          <div>
            <h2> </h2>
          </div>
        </SectionWraper>
      </ContentBox>
    </>
  );
}
