import { format } from "date-fns";
// import PropTypes from "prop-types";
import jsPDF from "jspdf";
import "jspdf-autotable";
import montserratFont from "../assets/fonts/montserrat/Montserrat-Medium.ttf";


export async function createProductOrder(products) {
    const pdf = new jsPDF();
    // src\assets\fonts\montserrat\Montserrat-Medium.ttf
    pdf.addFont(
      montserratFont,
      "Montserrat",
      "normal",
      "Identity-H"
    );
    pdf.setFont("Montserrat");

    pdf.text(
      `   ${products.id}  ${format(
        new Date(products.created_at),
        "dd-MM-yyyy"
      )}`,
      20,
      20
    );
    
    //    
    const columns = ["", ""];
    const data = [
      ["", products.product_name],
      ["", products.size],
      ["", products.quantity],
      ["", products.option],
      [" ", products.productionTime],
    ];
    // const data = products.map(product => [product.product_name, product.quantity, product.price_per_item, product.total_price]);

    pdf.autoTable({
      head: [columns],
      body: data,
      startY: 25,
      styles: {
        font: "Montserrat", //  'times', 'courier'
        fontStyle: "normal", //  'bold', 'italic', 'bolditalic'
        halign: "left", //  'center', 'right'
        valign: "middle", //  'middle', 'bottom'
        fontSize: 10,
        lineWidth: 0.1, //  0,   
      },
    });
    const textY = pdf.autoTable.previous.finalY + 20;
    pdf.text(
      `    ${products.id}  ${format(
        new Date(products.created_at),
        "dd-MM-yyyy"
      )}`,
      20,
      textY
    );
   
    pdf.autoTable({
      head: [columns],
      body: data,
      startY: textY + 5,
      styles: {
        font: "Montserrat",
        fontStyle: "normal",
        lineWidth: 0.1,
        halign: "left",
        valign: "middle",
      },
    });


    pdf.save(`: ${products.id}`);
  }

   